.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.pokemon-details {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 20px;
}

.pokemon-details__header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.pokemon-details__name {
  margin: 0;
}

.pokemon-details__image {
  height: 100px;
}

.pokemon-details__stats {
  display: flex;
  justify-content: space-between;
}

.pokemon-details__stats-column {
  width: 50%;
}

.pokemon-details__stats h3 {
  margin-bottom: 10px;
}

.pokemon-details__stats ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.pokemon-details__stats ul li {
  margin-bottom: 5px;
}

nav * {
  margin: 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

button {
  width: 100px;
  height: 40px;
  background-color: #b1b1b1;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 10px;
  
}

.pokemon-button {
  display: inline-block;
  padding: 8px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  text-decoration: none;
  color: #fff;
  background-color: #bababa;
}

.welcome-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.welcome-heading {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
  color: #333;
}

.welcome-subheading {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
  color: #666;
}

.welcome-button:hover {
  background-color: #555;
}

.image-div {
  height: 200px;
  width: 200px;
}

.container {
  height: 200px;
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  margin-top: 15px;
  margin-left: 15px;
  margin-right: 15px;
}

.page-buttons{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

p {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
